"use client";

import { MapContainer, TileLayer, Marker, Polyline, Circle } from "react-leaflet";
import L from "leaflet";
import { useEffect } from "react";

const DEFAULT_CENTER: [number, number] = [37.7749, -122.4194];

const droneRoute: [number, number][] = [
  [37.7758, -122.4194],
  [37.7769, -122.417],
  [37.7782, -122.4154],
  [37.7791, -122.4132],
];

const droneMarkers: [number, number][] = [
  [37.7766, -122.418],
  [37.7786, -122.4143],
  [37.7744, -122.4158],
];

const geofenceCenter: [number, number] = [37.7768, -122.4162];

const markerIcon = new L.Icon({
  iconUrl: new URL("leaflet/dist/images/marker-icon.png", import.meta.url).toString(),
  iconRetinaUrl: new URL(
    "leaflet/dist/images/marker-icon-2x.png",
    import.meta.url
  ).toString(),
  shadowUrl: new URL("leaflet/dist/images/marker-shadow.png", import.meta.url).toString(),
  iconSize: [25, 41],
  iconAnchor: [12, 41],
  shadowSize: [41, 41],
});

export default function MiniMap() {
  useEffect(() => {
    L.Icon.Default.mergeOptions({
      iconUrl: markerIcon.options.iconUrl,
      iconRetinaUrl: markerIcon.options.iconRetinaUrl,
      shadowUrl: markerIcon.options.shadowUrl,
    });
  }, []);

  return (
    <MapContainer
      center={DEFAULT_CENTER}
      zoom={13}
      scrollWheelZoom={false}
      attributionControl={false}
      zoomControl={false}
    >
      <TileLayer
        url="https://{s}.basemaps.cartocdn.com/dark_all/{z}/{x}/{y}{r}.png"
        attribution="&copy; OpenStreetMap contributors &copy; CARTO"
      />
      {droneMarkers.map((pos, index) => (
        <Marker key={`drone-${index}`} position={pos} icon={markerIcon} />
      ))}
      <Polyline positions={droneRoute} pathOptions={{ color: "#60a5fa", weight: 3 }} />
      <Circle
        center={geofenceCenter}
        radius={520}
        pathOptions={{ color: "#38bdf8", fillColor: "#38bdf8", fillOpacity: 0.12 }}
      />
    </MapContainer>
  );
}
